@import '../../../general/main.scss';
@import '../../../general/buttons.scss';
// @import '../../../general/NavigationBar.scss';

@import '_converterToolBarHelper.scss';

@mixin ToolBar_Image_Main() {
    > .ToolBar_ImageMain {
        @include ToolBar_ListElement();
        @include ToolBar_ListElement_Image();
    }
}

@mixin ToolBar_ListElement_Image {
    > .ToolBar_ListElement_Image {
        position: relative;
        &[state*='passive']{
            > .S-SliderMain {
                max-height: 0px;
                margin: 0;
                overflow: hidden;
                // display: none;

            }
            > .buttonsDiv {
                max-height: 0px;
                margin: 0;
                // display: none;
            }
        }
        >.imgDiv {
            display: block;
            box-sizing: border-box;
            position: relative;
            width: 100%;
            @include box-shadow();
            // padding: 0.25em;
            > img {
                box-sizing: border-box;
                position: relative;
                width: 100%;
                display: block;
            }
            > .spinner {
                margin: 0;
                position: absolute;
                z-index: 1;
                // padding: 0.5em;
                box-sizing: border-box;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: transparent;
                //visibility: hidden;
                &[state*='active']{
                    visibility: visible;
                }
                > div {
                    top: 50%; 
                    left: 50%;
                    transform: translate(-50%,-50%);
                    position: relative;
                    aspect-ratio: 1/1;
                    display: block;
                    width: auto;
                    height: auto;
                    max-height: 100%;
                    max-width: 100%;;
                }
            }
        }
        > .buttonsDiv {
            display: flex;
            justify-content: space-around;
            margin-top: 0.8vh;
            margin-bottom: 0.5vh;
            
            height: auto;
            max-height: max-content;
            // padding-top: 0.4rem !important;
            // padding-bottom: 0.4rem !important;
            overflow: hidden;
            transition: all 0.2s ease-in;
        }
    }
}