@import '../../../general/main.scss';
@import '../../../general/buttons.scss';
// @import '../../../general/NavigationBar.scss';

@import '_converterToolBarHelper.scss';

@mixin ToolBar_Text_Main() {
    > .ToolBar_TextMain {
        @include ToolBar_ListElement();
        @include ToolBar_ListElement_Text();
    }
}

@mixin ToolBar_ListElement_Text {
    > .ToolBar_ListElement_Text {
        position: relative;
        &[state*='passive']{
            height: 6em;
            > .S-SliderMain {
                display: none;
                overflow: hidden;
            }
            > .TextInputDiv {
                height: calc(100% - 1.5em);
                box-sizing: content-box;
                // display: none;
            }
            > .buttonsDiv {
                display: none;
            }
            > .DropDownInputMain_S {
                display: none;
            }
        }
        > .TextInputDiv {
            position: relative;
            overflow: hidden;
            @include box-shadow();
            margin: 0;
            height: fit-content;
            // padding: 0.25em;
            overflow: auto;
            max-height: 20em;
            >*{
                box-sizing: border-box;
            }
            > label, hr {
                display: none;

            }
            > textarea {
                position: relative;
                border: none;
                resize: none;
                overflow: hidden;
                // vertical-align: bottom;
                box-sizing: border-box;
                height: auto;
                width: 100%;
                padding-top: 0.5em;
                padding-bottom: 0.5em;
                padding-left: 0.5em;
            }
        }
        > .buttonsDiv {
            display: flex;
            justify-content: space-between;
            margin-top: 0.5em;
            > .AlignDiv {
                display: flex;
                border: 1px solid lightgray;
                border-radius: $border-radius;
                padding: 0.2em;
                > button {
                    display: flex;
                    align-items: center;
                    flex-direction: row;
                    flex-wrap: nowrap;
                    // justify-content: space-between;
                    > span {
                        // margin-top: auto;
                        // margin-bottom: auto;
                    }
                    // &[state*='active']{
                    //     > span {
                    //         color: $color-primary;
                    //     }
                    // }
                }
                // @include switchButton($color-primary);
            }
        }
        >.DropDownInputMain_S {
            margin-top: 0.5em;
            margin-bottom: 0.5em;
        }
        .S-SliderMain {
            margin-top: 0.4em;
            margin-bottom: 0.4em;
            height: auto;
            max-height: max-content;
            // padding-top: 0.4rem !important;
            // padding-bottom: 0.4rem !important;
            overflow: visible;
            transition: all 0.3s ease-in;

            &[drawDivider='true']{
                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 0px;
                    background: transparent;
                    border-bottom: 1px solid $color-devider;
                    left: 0;
                    // top: 50%;
                    margin-top: 0.25em;
                    position: relative;
                }
                &::before {
                    content: "";
                    display: block;
                    width: 100%;
                    height: 0px;
                    background: transparent;
                    border-top: 1px solid $color-devider;
                    left: 0;
                    // top: 50%;
                    margin-bottom: 0.2em;
                    position: relative;
                }
            }
            > .label {
                position: relative;
                width: 100%;
                display: inline-block;
                box-sizing: border-box;
                text-align: left;
                > span {
        
                }
            }
            > .contentBody {
                height: 2.1em;
                display: flex;
                flex-direction: column;
        
                > .sliderBody {
                    order: 1;
                    height: 100%;
                    > input[type="range"] {
                        width: 100%;
                        position: relative;
                        font-size: 1em;
                        height: 100%;
                        display: inline-block;
                        outline: none;
                        // align-items: center;
                        -webkit-appearance: none;
                        transition: box-shadow 0.2s ease;
                        &::-webkit-slider-runnable-track {
                            box-sizing: border-box;
                            // top: 45%;
                            width: 100%;
                            // margin-inline: -5%;
                            height: 0.15em;
                            // margin-top: 0.25em;
                            // margin-top: 45%;
                            // margin-bottom: 45%;
                            background: black;
                            border: none;
                            border-radius: 10px;
                        }
                        &::-webkit-slider-thumb {
                            // z-index: 2;
                            -webkit-appearance: none;
                            appearance: none;
                            border: none;
                            vertical-align: baseline;
                            height: 1em;
                            width: 0.5em;
                            // border-radius: 10%;
                            margin-top: -0.425em;
                            background: $color-primary-Dark;
                            
                            // margin-top: calc(-100% + 0.5em);
                            box-sizing: border-box;
                            // @include border();
                            transition: box-shadow 0.2s ease;
                        }
                        &:focus {
                            &::-webkit-slider-thumb {
                                @include box-shadow(0, $color-primary-Dark, 0.7);
                            }
                            outline: none;
                        }
                        
                        &:hover::-webkit-slider-runnable-track {
                            @include box-shadow(0, gray);
                        }
                    }
                    > datalist {
                        display: none;
                        > option {
                        }
                    }
                }
                > .valueOutput {
                    position: relative;
                    order: 2;
                    font-size: 1.1em;
                    height: 1.1em;
                    &[hidden='true']{
                        display: none;
                    }
                    top: -0.1em;
                    width: 1.5em;
                    display: inline-block;
                    box-sizing: border-box;
                    text-align: center;
                    margin-top: auto;
                    > span {
                    }
                }
            }
        }
    }
}